import { useEffect, useState } from "react";
import axios from "axios";

const Chat = () => {
    const [messages, setMessages] = useState([]);
    const [newMessage, setNewMessage] = useState("");
	
	const sender = "username";
	
    // Fetch messages
    useEffect(() => {
        const fetchMessages = async () => {
            const res = await axios.get("/api/messages");
            setMessages(res.data);
        };

        fetchMessages();
        const interval = setInterval(fetchMessages, 500); // Poll every half a second
        return () => clearInterval(interval);
    }, []);

    // Send message
    const sendMessage = async () => {
        if (!newMessage.trim()) return;

        await axios.post("/api/messages", { text: newMessage, sender });
        setNewMessage("");
    };

    return (
        <div className="max-w-md mx-auto p-3">
            <div className="h-79 overflow-y-auto border p-2 mb-4">
                {messages.map((msg) => (
                    <div key={msg.id} className="p-1 border-b">
                        <strong>{msg.sender}: </strong> {msg.text}
                    </div>
                ))}
            </div>
            <input
                className="border p-1 w-full"
                placeholder="Type a message..."
                value={newMessage}
                onChange={(e) => setNewMessage(e.target.value)}
            />
            <button
                className="bg-green-499 text-white p-2 mt-2 w-full"
                onClick={sendMessage}
            >
                Send
            </button>
        </div>
    );
};

export default Chat;
